define(function(){
    var canvas;
    var comments = [];
    var globalAlpha = 1;
    function bind(obj){
        obj = obj.getContext ?obj.getContext("2d") : obj;
        if(obj instanceof CanvasRenderingContext2D){
            canvas = obj;
        }
        else{
            console.error("canvas binding error");
        }
    }
    // function setDefault(comObj,def){
    //     var temp = {};
    //     if(comObj["text"]){
    //         temp["text"] = comObj["text"];
    //     }
    //     else{
    //         return;
    //     }
    //     for(var name in def){
    //         temp[name] = comObj[name] == null ? def[name] : comObj[name];
    //     }
    //
    //     return temp;
    // }
/**
 * comObj
 * @property text 
 * @property x    
 * @property y
 * @property color 
 * @property opacity 
 * @property size 
*/
    function add(_comments){
        comments = _comments;
    }
    function clear(){
        comments = [];
        canvas.clearRect(0,0,canvas.canvas.width,canvas.canvas.height);
        setFont({size:null});
    }
    function remove(comment){
        var p = comments.indexOf(comment);
        if(p!= -1){
            comments.splice(p,1);
        }
    }
    /*function setFont(comObj){
        canvas.font ="bold " + comObj.size +"px"+" sans-serif";
    }*/
    var setFont = function(){
        var size;
        return function(comObj){
            if(size != comObj.size){
                size = comObj.size;
                canvas.font = size +"px"+" Microsoft YaHei";
            }
        };
    }();
    var setColor = function(comObj){
        var colorCache = {};
        return function(comObj){
            var color;
            if(colorCache[comObj.color]){
                color = colorCache[comObj.color];
            }
            else{
                color = comObj.color.toString(16);
                color = color.length >= 6 ? color : new Array(6 - color.length + 1).join("0") + color;
                color = "#" + color;
                colorCache[comObj.color] = color;

            }
            canvas.fillStyle = color;
        };
    }();
    function setAlpha(opacity){
        canvas.globalAlpha = opacity;
    }
    function draw(){
        var staticComment = [];
        canvas.textAlign = "start";
        comments.forEach(function(comObj,index){
            var x = comObj.align_x;
            if(x === "middle"){
                x = canvas.canvas.width/2;                //
                staticComment.push({
                  x:x,
                  comObj:comObj
                });
                // canvas.textAlign = "center";
            }
            // else{
            //     canvas.textAlign = "start";
            // }
            else{
              setFont(comObj);
              setColor(comObj);
              canvas.fillText(comObj.text,x,comObj.align_y+comObj.height);
            }
        });
        canvas.textAlign = "center";
        staticComment.forEach(function(item,index){
            setFont(item.comObj);
            setColor(item.comObj);
            canvas.fillText(item.comObj.text,item.x,item.comObj.align_y+item.comObj.height);
        });
    }
    function drawVideo(video,width,height){
      canvas.drawImage(video,0,0,width,height);
    }
    function getWidth(comment){
        if(!canvas){
            canvas = document.createElement("canvas").getContext("2d");
        }
        setFont(comment);
        return canvas.measureText(comment.text).width;
    }
    return{
        bind:bind,
        add:add,
        draw:draw,
        drawVideo:drawVideo,
        setAlpha:setAlpha,
        clear:clear,
        getWidth:getWidth
    };
});



